@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
body{
  font-family: "Rubik";
}

.background{
  position: absolute;
  z-index: 0;
  width: 100vw;
}

.App {

  width: 100vw;
  height: 100vh;
  background-color: rgb(30, 27, 34);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
h1{  
  z-index: 1;
  color: white;
  box-shadow: 2px 10px 5px 0px rgba(255,255,255,0.75);
-webkit-box-shadow: 2px 10px 5px 0px rgba(255,255,255,0.75);
-moz-box-shadow: 2px 10px 5px 0px rgba(255,255,255,0.75);
}
.cardsSec{
  z-index: 1;
  width: 90%;
  /* background-color: rgb(101, 156, 178); */
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.section{
  width: 25rem;
  height: 25rem;
  background-color: aqua;
  margin: 1rem;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .4s;
  cursor: pointer;
}
.section:hover{
  transition: .4s;
  width: 27rem;
  height: 27rem;
}